<template  >
  <div id="body">
   <v-main id="main-page">

     <v-row> 
        <v-col id="logo-container" cols="12" contain>

          <v-img class="logo"
                src="../assets/ao_gallery_logo.png"
                max-height="300"
                max-width="500"
                contain              
                >
          
          </v-img>        

        </v-col>
      </v-row>

      <h1 id="teaser-heading">COMING SOON</h1>
      
      <div class="container">
        <div v-if="loaded" id="countdown">
          <ul>
            <li><span >{{displayDays}}</span>days</li>
            <li><span >{{displayHours}}</span>Hours</li>
            <li><span >{{displayMinutes}}</span>Minutes</li>
            <li><span >{{displaySeconds}}</span>Seconds</li>
          </ul>
        </div>    
      </div>
  
    
    </v-main> 
  </div>

</template>

<script>
// @ is an alias to /src


export default {
  name: 'Home',

  data(){
    return{
      displayDays:"0",
      displayHours:"0",
      displayMinutes:"0",
      displaySeconds:"0",
      loaded : false
    }
  }, 
  computed:{
    _seconds: ()=> 1000,
    _minutes(){
      return this._seconds * 60
    }, 
    _hours(){
      return this._minutes * 60
    },
    _days(){
      return this._hours * 24
    }

  },
  mounted(){
    this.showRemaining();
  },
  methods:{

    showRemaining(){

      const timer = setInterval(()=>{
        const now = new Date();
        const end = new Date(2021, 3, 22, 10, 10, 10, 10)
        const distance = end.getTime() - now.getTime();
        
        if(distance<0){
          clearInterval(timer);
          return
        }

        const days = Math.floor((distance/this._days));
        const hours = Math.floor((distance % this._days) / this._hours);
        const minutes = Math.floor((distance % this._hours) / this._minutes);
        const seconds = Math.floor((distance % this._minutes) / this._seconds);

        this.displayDays = days < 10 ?'0' + days:days;
        this.displayHours = hours < 10 ?'0' + hours:hours;
        this.displayMinutes = minutes < 10 ?'0' + minutes:minutes;
        this.displaySeconds = seconds < 10 ?'0' + seconds:seconds;

        this.loaded = true
      })

    }
   
    }

  }



</script>

